.products__section{
    background-color: var(--container);
    width: 510px;
    margin: 30px auto;
    padding: 24px;
    min-height: 400px;
    border-radius: 16px;
}

.products_title{
    
    margin-bottom: 16px;
    color: var(--text);

}

.products__container{
    display: flex;
    flex-wrap: wrap;
    gap: 14px;

}

@media (min-width:760px){
    .products__section{
        width: 740px;
    }
}

@media (min-width:990px){
    .products__section{
        width: 970px;
    }
}


